/* Opportunities.css */

/* Basic reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Container styling */
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
}

/* Opportunity card styling */
.oppurtunity-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #f9f9f9;
    transition: transform 0.3s;
}

.oppurtunity-card:hover {
    transform: scale(1.02);
}

.oppurtunity-card h1 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.oppurtunity-card p {
    margin-bottom: 10px;
    font-size: 1rem;
}

.oppurtunity-card button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background 0.3s;
}

.oppurtunity-card button:disabled {
    background-color: #ddd;
    cursor: not-allowed;
}

.oppurtunity-card button:hover:not(:disabled) {
    background-color: #0056b3;
}

/* Responsive styling */
@media (max-width: 768px) {
    .oppurtunity-card {
        padding: 15px;
    }

    .oppurtunity-card h1 {
        font-size: 1.2rem;
    }

    .oppurtunity-card p {
        font-size: 0.9rem;
    }

    .oppurtunity-card button {
        padding: 8px 16px;
    }
}

@media (max-width: 480px) {
    .oppurtunity-card {
        padding: 10px;
    }

    .oppurtunity-card h1 {
        font-size: 1rem;
    }

    .oppurtunity-card p {
        font-size: 0.8rem;
    }

    .oppurtunity-card button {
        padding: 6px 12px;
    }
}